import React, { FC }  from 'react';
import './App.css';
import Navbar from './components/Navbar';
import Navigation from './components/Navigation';
import NavigationDesktop from './components/NavigationDesktop';
import Horse from './components/Horse';
import HorseDesktop from './components/HorseDesktop';
import Horses from './horsesAPI';
import LoadingOverlay from 'react-loading-overlay';
import {
  Center,
  Grid, GridItem
} from '@chakra-ui/react';
import Results from './components/Results';
import OrderSheet from './components/OrderSheet';
import MyBets from './components/MyBets'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const horses = new Horses();
const heartbeat = 5000;
function App() {
  const [initialized, setInitialized] = React.useState(false);
  const [loading, setLoading] = React.useState(true);
  const [polling, setPolling] = React.useState(false);
  const [tracks, setTracks] = React.useState([]);

  const [race, setRace] = React.useState(0);
  const [raceId, setRaceId] = React.useState(0);
  const [horseElements, setHorseElements] = React.useState([]);
  const [horseDesktopElements, setHorseDesktopElements] = React.useState([]);
  const [isChecked,setisChecked] = React.useState(false);

  React.useEffect(() => {
    (async () => {
      const openTracks = await horses.getOpenTracks()
      setTracks(openTracks);
      setInitialized(true);
      setRaceId(openTracks[0].races[0].race_id);
    })();
  },[]);

  React.useEffect(() => {
    let interval;
    if ( initialized ) {
      setLoading(true);
      setPolling(false);
      (async () => {
        const data = await horses.getRace(raceId);
        setRace(data);
        updateHorseElements(data);
        updateHorseElementsDesktop(data);
        interval = setInterval(() => {
          updateOdds(data);
        }, heartbeat);
        setLoading(false);
      })();
      return () => clearInterval(interval);
    }
  },[raceId,isChecked]);

  function updateHorseElements(race) {
    setHorseElements(race.runners.map((horse, index) => {
      return <Horse
        race={race}
        position={index+1}
        horse={horse}
        key={index}
        polling={false}
        isChecked={isChecked} 
      />;
    }));
  }

  function updateHorseElementsDesktop(race) {
    setHorseDesktopElements(race.runners.map((horse, index) => {
      return <HorseDesktop
        race={race}
        position={index+1}
        horse={horse}
        key={index}
        polling={false}
        isChecked={isChecked} 
      />;
    }));
  }

  function updateRaceId(raceId) {
    setRaceId(raceId);
  }

  async function updateOdds(race) {
    setPolling(true);
    const updatedOdds = await horses.getOdds(race.race_id);
    const newRunners = race.runners.map((raceHorse, index) => {
      const updatedOdd = updatedOdds.find((oddsHorse) => raceHorse.horse_id === oddsHorse.horse_id);
      return updatedOdd ? {
        ...raceHorse,
        h_curr_odds: updatedOdd.h_curr_odds,
      } : raceHorse;
    });

    const newRace = {
      ...race,
      runners: newRunners,
      timestamp: Date.now(),
    }

    setRace(newRace);
    updateHorseElements(newRace);
    updateHorseElementsDesktop(newRace);
  }

    const [isMobile, setisMobile] = React.useState(true);
    window.addEventListener('load',function(){
      if(!!navigator.userAgent.match(/AppleWebKit.*Mobile.*/)){
        setisMobile(true)
      } else if(!navigator.userAgent.match(/AppleWebKit.*Mobile.*/)){
        setisMobile(false)
      }
  })
  

    

  return (
    <LoadingOverlay
      active={loading}
      spinner
      styles={{
        overlay: (base) => ({
          ...base,
          background: 'rgba(255, 255, 255, 0.5)',
          position: 'fixed',
        }),
        spinner: (base) => ({
          ...base,
          width: '75px',
          '& svg circle': {
            stroke: 'rgba(0, 0, 0, 0.5)'
          }
        })
      }}
    >

      {isMobile ?       
      <div className="App">
          <Navbar />
          <Navigation tracks={tracks} updateRaceId={updateRaceId} race={race} polling={polling} isChecked={isChecked} setisChecked={setisChecked}/>
          <div className="mobile-outer-wrap">
            {horseElements}
          </div>
      </div> : 
      <div className="App">
          <NavigationDesktop tracks={tracks} updateRaceId={updateRaceId} race={race} polling={polling} isChecked={isChecked} setisChecked={setisChecked}  />
          <Center>
            <Grid templateColumns={"repeat(10,1fr)"} gap={2} width={'1440px'}>
              <GridItem colSpan={7}>
                <DragDropContext>
                {horseDesktopElements}
                </DragDropContext>
                </GridItem>
              {
                race.race_status === 'FINAL'?
                <GridItem colSpan={3}><OrderSheet/><MyBets/><Results raceId={race.race_id}/></GridItem> : 
                <GridItem colSpan={3}><OrderSheet/><MyBets/></GridItem>
                
              }
            </Grid>
          </Center>

            
      </div>}

    </LoadingOverlay>
  );
}

export default App;
