import React from 'react';
import {
  Box,
  Center,
  Collapse,
  IconButton,
  Text,
} from '@chakra-ui/react';
import { ChevronDownIcon, ChevronUpIcon, ChevronLeftIcon, ChevronRightIcon } from '@chakra-ui/icons';

// pp_3/16  //    3/16
// pp_1/4   //    4/16
// pp_3/8   //    6/16
// pp_1/2   //    8/16
// pp_3/4   //   12/16
// pp_1m    // 1
// pp_11/4  // 1  4/16
// pp_13/8  // 1  6/16
// pp_11/2  // 1  8/16
// pp_13/4  // 1 12/16
// pp_2m    // 2
// pp_21/4  // 2  4/16
// pp_21/2  // 2  8/16
// pp_23/4  // 2 12/16

export default function HorseHistory(props) {
  const [perfPage, setPerfPage] = React.useState(0);
  const [workPage, setWorkPage] = React.useState(0);

  // React.useEffect(() => {
  //   console.log(perfPage);
  // },[perfPage]);

  React.useEffect(() => {
    // console.log(workPage);
  },[workPage]);

  const timeKeys = [
    'pp_3/16',
    'pp_1/4',
    'pp_3/8',
    'pp_1/2',
    'pp_3/4',
    'pp_1m',
    'pp_11/4',
    'pp_13/8',
    'pp_11/2',
    'pp_13/4',
    'pp_2m',
    'pp_21/4',
    'pp_21/2',
    'pp_23/4',
  ];

  const times = (race) => {
    const timeElements = timeKeys.map((key, i) => {
      if ( race[key] ) {
        const timePieces = race[key].split("");
        const time = timePieces[0];
        const exponent = timePieces.length > 1
          ? <sup>{timePieces.slice(1)}</sup>
          : ``;

        return (
          <>
            {time}{exponent}&nbsp;
          </>
        );
      }
    });

    return timeElements;
  }

  const topHorses = (race) => {
    if ( race.top_horses ) {
      const horses = race.top_horses
        ? race.top_horses.split(',')
        : [];

      const distances = race.top_horse_fin_distance
        ? race.top_horse_fin_distance.split(',')
        : [];

      const topHorseElements = horses.map((horse, i) => {
        const exponent = distances[i]
          ? <sup>{distances[i]}</sup>
          : ``;
        return (
          <>
            {horse}{exponent}&nbsp;
          </>
        );
      });

      return topHorseElements;
    }
  }

  const workouts = () => {
    const workoutElements = props.workouts.map((workout, i) => {
      return (
        <Text as='span' display={ ((i >= workPage) && (i <= workPage+1)) ? 'block' : 'none' }>
          <Text as='span' fontWeight={600} m={1}>{workout.workout_date}</Text>
          {workout.track_code} {workout.track_dist} {workout.track_type} {workout.time} {workout.notes} {workout.rank}
        </Text>
      );
    });

    return workoutElements;
  }

  const history = props.history.map((race, i) => {
    return (
      <Box py={1} mr={'10%'} display={ ((i >= perfPage) && (i <= perfPage+2)) ? 'block' : 'none' }>
        <b>{race.race_date}</b> {race.track_code} {race.race_num} {race.track_type} {race.race_track_dist} {race.race_class} {race.purse_usd_size} {race.fractional_times}<br />
        {race.pgm} {times(race)} {race.jockey_name} {race.weight} {race['m/e']}<br />
        {topHorses(race)} {race.comments}
      </Box>
    );
  });

  const perfPageUp = () => {
    if ( perfPage > 0 ) {
      setPerfPage(perfPage-1);
    }
  }

  const perfPageDown = () => {
    if ( perfPage <= props.history.length - 4 ) {
      setPerfPage(perfPage+1);
    }
  }

  const workPageUp = () => {
    if ( workPage > 0 ) {
      setWorkPage(workPage-1);
    }
  }

  const workPageDown = () => {
    if ( workPage <= props.workouts.length - 3 ) {
      setWorkPage(workPage+1);
    }
  }

  return (
    <>

      <Box mb={2}>
        <Collapse in={props.isOpen} animateOpacity >
          <Box px={2} fontSize='xs' position='relative'>
            {
              props.history.length
              ?
                <>
                  <Box position='absolute' right={'2px'} top={'25%'} >
                    <IconButton
                      icon={<ChevronUpIcon m={0} w={10} h={10} />}
                      className='horseButton'
                      onClick={perfPageUp}
                      disabled={!perfPage}
                      display={props.history.length > 3 ? 'block' : 'none'}
                    />
                    <br />
                    <IconButton
                      icon={<ChevronDownIcon m={0} w={10} h={10} />}
                      className='horseButton'
                      onClick={perfPageDown}
                      disabled={perfPage > props.history.length - 4}
                      display={props.history.length > 3 ? 'block' : 'none'}
                    />
                  </Box>
                  {history}
                </>
              :
                <Center>
                  <Text m={2}><i>No Past Performances</i></Text>
                </Center>
            }

            {/*<Box py={1}>
              <b>04/07/22</b>  Aqu Fm 1 15K  1:14<sup>2</sup> 1:41<sup>1</sup> 1:55<br />
              <b>60</b> 3/11 9<sup>4</sup> 9<sup>4</sup> 10<sup>4</sup> 7<sup>4</sup> 6<sup>4</sup> Salgado L 118b<br />
              Erlyvoting1 Unojo3 Gilldag2 Inch Straight No response<br />
            </Box>
            <Box py={1}>
              <b>04/07/22</b>  Aqu Fm 1 15K  1:14<sup>2</sup> 1:41<sup>1</sup> 1:55<br />
              <b>60</b> 3/11 9<sup>4</sup> 9<sup>4</sup> 10<sup>4</sup> 7<sup>4</sup> 6<sup>4</sup> Salgado L 118b<br />
              Erlyvoting1 Unojo3 Gilldag2 Inch Straight No response<br />
            </Box>
            <Box py={1}>
              <b>04/07/22</b>  Aqu Fm 1 15K  1:14<sup>2</sup> 1:41<sup>1</sup> 1:55<br />
              <b>60</b> 3/11 9<sup>4</sup> 9<sup>4</sup> 10<sup>4</sup> 7<sup>4</sup> 6<sup>4</sup> Salgado L 118b<br />
              Erlyvoting1 Unojo3 Gilldag2 Inch Straight No response<br />
            </Box>*/}
            <Box>
              <Center>
                {
                  props.workouts.length?
                    <>
                      <IconButton
                        icon={<ChevronLeftIcon m={0} w={7} h={7} />}
                        className='horseButton'
                        onClick={workPageUp}
                        disabled={!workPage}
                      />
                      <b>Works:</b>
                      {workouts()}
                      <IconButton
                        icon={<ChevronRightIcon m={0} w={7} h={7} />}
                        className='horseButton'
                        onClick={workPageDown}
                        disabled={workPage > props.workouts.length - 3}
                      />
                    </>
                  :
                    <Text m={2}><i>No Workouts</i></Text>
                }
              </Center>
            </Box>
          </Box>
        </Collapse>
      </Box>
    </>
  );
}
