import React from 'react';
import {
    Box,
    Input,
    Select,
    SimpleGrid,
    Stack,
    Switch,
    Text,
    Button,
    Center,
    cookieStorageManager
  } from '@chakra-ui/react';
  import axios from 'axios';

export default function Results(props) {
  const [result, setResult] = React.useState('');

  let getResults = async() => {
    axios.get(`http://ibethorses.com/request/api/v1/results?race_id=${props.raceId}`)
    .then(res => {setResult(res.data.bet_results)})
  }
  getResults()
  var loopData = []

  if(result){
    for(var i=0;i<result.length; i++){
      loopData[i] = result[i]
    }
  }


  return (
    <>
      <Box textAlign='center'>
        <Box borderWidth='1px' borderRadius='lg' p='2' marginRight='3'>
        <Text fontSize="20"><b>Race Results</b></Text>
        <SimpleGrid columns={4} spacing={1} fontSize='xs'>
          <Text>base_wager_amount</Text>
          <Text>bet_type</Text>
          <Text>payout_odds</Text>
          <Text>pgm_numbers</Text>
        </SimpleGrid>
          {loopData.map(obj=>
            <SimpleGrid columns={4} spacing={0} fontSize='xs'>
              <Text>{obj.base_wager_amount}</Text>
              <Text>{obj.bet_type}</Text>
              <Text>{obj.payout_odds}</Text>
              <Text>{obj.pgm_numbers}</Text>
            </SimpleGrid> 

            )}
        
        </Box>

      </Box>
    </>
  );
}