import axios from 'axios';

export default class Horses {
  async getOpenTracks() {
    //console.log('getOpenTracks');
    //console.log('http://ibethorses.com/request/api/v1/tracks/open/select?offset=-1');
    const resOpen = await axios.get('http://ibethorses.com/request/api/v1/tracks/open/select?offset=-1');
    const resFinal = await axios.get('http://ibethorses.com/request/api/v1/tracks/final/select?offset=-1');

    const dataOpen = resOpen.data.PAYLOAD.result_arr;
    const dataFinal = resFinal.data.PAYLOAD.result_arr;
    
    const data = dataOpen.concat(dataFinal)
    //const data = dataFinal

    //console.log(data);
    if ( ! data.length ) {
      return [];
    }

    const trackObjects = data.reduce((tracks, race) => {
      tracks[race.track_id] = tracks[race.track_id] || {};
      tracks[race.track_id].races = tracks[race.track_id].races || [];

      tracks[race.track_id].track_id = race.track_id;
      tracks[race.track_id].track_name = race.track_name;
      tracks[race.track_id].races.push({
        // info: race.info,
        mtp: race.mtp,
        dt_est_start: race.dt_est_start,
        race_id: race.race_id,
        race_num: race.race_num,
        // race_status: race.race_status,
        // start_time: race.start_time,
        // status: race.status,
        // track_id: race.track_id,
        // track_name: race.track_name,
        // v_cnt: race.v_cnt,
      });

      return tracks;
    }, {});

    let tracks = Object.keys(trackObjects).map(id => {
      return trackObjects[id];
    });

    tracks = tracks.sort(function(a, b) {
      return a.track_name.localeCompare(b.track_name);
    });

    return tracks;
  }

  async getRace(race_id) {
    // race_id = 121364;
    //console.log('getRace');
    //console.log(`http://ibethorses.com/request/api/v1/race/id/info/live/get?race_id=${race_id}`);
    const res = await axios.get(
      `http://ibethorses.com/request/api/v1/race/id/info/live/get?race_id=${race_id}`
    );

    const data = res.data.PAYLOAD.result_arr[0];
    data.timestamp = Date.now();

    data.runners = data.runners.map((runner) => {
      return {
        ...runner,
      }
    });

    return data;
  }

  async getOdds(race_id) {
    // race_id = 121364;
    //console.log('getOdds');
    //console.log(`http://ibethorses.com/request/api/v1/race/horse/odds?race_id=${race_id}`);
    const res = await axios.get(
      `http://ibethorses.com/request/api/v1/race/horse/odds?race_id=${race_id}`
    );

    const data = res.data.PAYLOAD.result_arr;

    return data;
  }
}
