import React,{ useEffect, useState } from 'react';
import {
  Box,
  Input,
  Select,
  SimpleGrid,
  Stack,
  Switch,
  Text,
  Button
} from '@chakra-ui/react';
import moment from 'moment';
import Results from './Results'

function localize(timestamp) {

  // const now = new Date();
  // const date = new Date(timestamp * 1000);

  // console.log(date-now);


  // let hours = date.getHours();
  // let minutes = date.getMinutes();
  // let seconds = date.getSeconds();

  // minutes = minutes < 10 ? `0${minutes}` : minutes;
  // seconds = seconds < 10 ? `0${seconds}` : seconds;
  // return `${hours}:${minutes}:${seconds}`;


  const now = moment();
  const date = moment(timestamp*1000);
  const mtp = date.diff(now, 'minutes');
  

  if ( mtp >= 60 || mtp <= 0  ) {
    return date.format('h:mm A');
  }

  return `${mtp} MTP`;
}

export default function Navigation(props) {

  const [raceOptions, setRaceOptions] = React.useState([]);
  const [currentRace, setCurrentRace] = React.useState('');
  const [currentTrack, setCurrentTrack] = React.useState('');
  const [mtp, setMTP] = React.useState('');
  const [fadeClass, setFadeClass] = React.useState('');
  

  const trackOptions = props.tracks.map((track,index) => {
    return <option value={track.track_id} key={index}>{track.track_name}</option>;
  });

  const betOptions = props.race.bet_types && props.race.bet_types.map((betType,index) => {
    return <option value={betType} key={index}>{betType}</option>;
  });

  React.useEffect(() => {
    setRaceOptions(props.tracks[0] && props.tracks[0].races.map((race,index) => {
      return <option value={race.race_id} key={index}>Race {race.race_num}</option>;
    }));
    props.tracks[0] && setCurrentTrack(props.tracks[0]);
    props.tracks[0] && setCurrentRace(props.tracks[0].races[0]);
  },[props.tracks]);

  React.useEffect(() => {
    props.updateRaceId(currentRace.race_id);
    setMTP(currentRace.dt_est_start ? `${localize(currentRace.dt_est_start)}` : '');
  },[currentRace]);

  React.useEffect(() => {
    setMTP(props.race.dt_est_start ? `${localize(props.race.dt_est_start)}` : '');
    // if ( props.polling ) {
    //   setFadeClass('m-fadeOut');
    //   setTimeout(function() {
    //     setFadeClass('m-fadeIn');
    //   }, 1000);
    // }
  },[props.race.timestamp]);

  function changeTrack(e) {
    const track = props.tracks.find(track => track.track_id === parseInt(e.target.value));
    setCurrentTrack(track);
    setCurrentRace(track.races[0]);
    setRaceOptions(track.races.map((race,index) => {
      return <option value={race.race_id} key={index}>Race {race.race_num}</option>;
    }));
  }

  function changeRace(e) {
    const race = currentTrack.races.find(race => race.race_id === parseInt(e.target.value));
    setCurrentRace(race);
  }

  var btnStatus = "none"
  var lstResults = "block"
  var raceStatus = ""
  var btnResult = "Back"
  var raceStatusShow='none'

  const [show, setShow] = useState(true);

  if(props.race.race_status === 'CANCELED'){
    raceStatus = "Race Cancelled"
    lstResults = "none"
    raceStatusShow = 'block'
  }
  else if(props.race.race_status === 'RACE_OFF'){
    raceStatus = "Race is off the Board"
    raceStatusShow = 'block'
  }
  else if(props.race.race_status === 'FINAL'){
    raceStatus = "Results are Official"
    btnStatus = "block"
    btnResult = "back"
    raceStatusShow = 'block'
  }


  if(show){
    btnResult = "Results"
  }

  return (
    <>
      <Box p={2}>
        <SimpleGrid columns={3} spacing={2}>
          <Box>
            <Select size='sm' value={currentRace.race_id} onChange={changeRace}>
              {raceOptions}
            </Select>
          </Box>
          <Box>
            <Select size='sm' value={currentTrack.track_id} onChange={changeTrack}>
              {trackOptions}
            </Select>
          </Box>
          <Box>
            <Text textAlign='center' fontSize='sm' lineHeight='32px'>
              <span className={fadeClass}>{mtp}</span>
            </Text>
          </Box>
        </SimpleGrid>
        <Stack spacing={0} textAlign='center'>
          {
            props.race
              ?
                props.race.race_details
                  ?
                    <Text fontSize='xs'>{props.race.race_details}</Text>
                  :
                    <>
                      <Text fontSize='sm'>{props.race.race_track_dist} |  {props.race.race_track_surf} | {props.race.race_class} | <b>Purse:</b> {props.race.purse_usd_size}</Text>
                      <Text fontSize='sm'><b>Wagers:</b> {props.race.bet_types ? props.race.bet_types.join(', ') : ''}</Text>
                    </>
              :
                <br />
          }
        </Stack>

        <div style={{display:raceStatusShow}}>
          <Box backgroundColor='#F8F5C2' p='1'>
            <Text textAlign="center"><b>{raceStatus}</b></Text>
            <Button onClick={() => setShow(prev => !prev)} margin="0 auto" height="1.6rem" style={{display:btnStatus}} color="white" px='5' backgroundColor="green.600" fontSize="sm">{btnResult}</Button>
          </Box>
        </div>

        &nbsp; 

        {show && <div style={{display:lstResults}}>
        <SimpleGrid columns={3} spacing={2}>
          <Box fontSize='xs' lineHeight='32px'>
            Basic        
            <Switch
            size='sm'
            colorScheme='green'
            onChange={()=>{props.setisChecked(!props.isChecked)}}
            isChecked={!props.isChecked}/>
            Advanced
          </Box>
          <Box>
            <Select size='sm' placeholder='Bet Type'>
              {betOptions}
            </Select>
          </Box>
          <Box>
            <Input size='sm' textAlign='center' xvalue='$2000' placeholder='Amount' />
          </Box>
        </SimpleGrid>
        </div>}
      </Box>

      {!show && <Box marginLeft='3'>
      <Results raceId={props.race.race_id}/>
      &nbsp; 
      </Box>}
    </>
  );
}
