import React from 'react';
import {
  Fade,
  Flex,
  Badge,
  Box,
  Select,
  useDisclosure,
  Grid,
  GridItem,
  Text,
  SimpleGrid,
  Center,
  HStack
} from '@chakra-ui/react';
import LinesEllipsis from 'react-lines-ellipsis'

function abbreviate(long) {
  if ( long ) {
    const parts = long.split(" ");
    const initial = parts[0] = long.split("")[0];
    const rest = parts.slice(1);
    return [parts[0], ...rest].join(" ");
  }
}

export default function HorseStats(props) {
  const [currentYear, setCurrentYear] = React.useState('');
  const [currentTrack, setCurrentTrack] = React.useState('');
  const [currentSurface, setCurrentSurface] = React.useState('');
  const [year1, setYear1] = React.useState('');
  const [year2, setYear2] = React.useState('');
  const [year3, setYear3] = React.useState('');
  const [track1, setTrack1] = React.useState('');
  const [track2, setTrack2] = React.useState('');
  const [track3, setTrack3] = React.useState('');
  const [surface1, setSurface1] = React.useState('');
  const [surface2, setSurface2] = React.useState('');
  const [surface3, setSurface3] = React.useState('');
  const [fadeClass, setFadeClass] = React.useState('');
  const [refreshedRace, setRefreshedRace] = React.useState(0);

  React.useEffect(() => {
    if ( currentYear == '' ) {
      const thisYear = `${new Date().getFullYear()}`;
      setCurrentYear(thisYear);
    }
    updateYear(currentYear);
  },[currentYear]);

  React.useEffect(() => {
    if ( currentTrack == '' ) {
      setCurrentTrack(props.race.track_code);
    }
    updateTrack(currentTrack);
  },[currentTrack]);

  React.useEffect(() => {
    if ( currentSurface == '' ) {
      setCurrentSurface(props.race.race_track_surf);
    }
    updateSurface(currentSurface);
  },[currentSurface]);

  React.useEffect(() => {
    if ( props.race.race_id == refreshedRace ) {
      setFadeClass('m-fadeOut');
      setTimeout(function() {
        setFadeClass('m-fadeIn');
      }, 1000);
    }
    setRefreshedRace(props.race.race_id);
  },[props.race.timestamp]);

  // This is s stop-gap, there needs to be some aggreements made between UI and Backend
  if (!props.horse.active_years) { props.horse.active_years = [] }
  if (!props.horse.active_tracks) { props.horse.active_tracks = [] }
  if (!props.horse.active_surfaces) { props.horse.active_surfaces = [] }


  const yearOptions = props.horse.active_years.map((year,index) => {
    return <option value={year} key={index}>{year}</option>;
  });

  const trackOptions = props.horse.active_tracks.map((track,index) => {
    return <option value={track} key={index}>{track}</option>;
  });

  const surfaceOptions = props.horse.active_surfaces.map((surface,index) => {
    return <option value={surface} key={index}>{surface}</option>;
  });

  const changeYear = (e) => {
    setCurrentYear(e.target.value);
  }

  const changeTrack = (e) => {
    setCurrentTrack(e.target.value);
  }

  const changeSurface = (e) => {
    setCurrentSurface(e.target.value);
  }

  const updateYear = (year) => {
    const key1 = `perc_horse_year_fin_place_${year}_1`;
    const key2 = `perc_horse_year_fin_place_${year}_2`;
    const key3 = `perc_horse_year_fin_place_${year}_3`;

    if ( typeof props.horse.runner_fin_place_perc[key1] !== undefined ) {
      setYear1(props.horse.runner_fin_place_perc[key1]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key2] !== undefined ) {
      setYear2(props.horse.runner_fin_place_perc[key2]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key3] !== undefined ) {
      setYear3(props.horse.runner_fin_place_perc[key3]);
    }
  }

  const updateTrack = (track) => {
    const key1 = `perc_horse_track_fin_place_${track}_1`;
    const key2 = `perc_horse_track_fin_place_${track}_2`;
    const key3 = `perc_horse_track_fin_place_${track}_3`;

    if ( typeof props.horse.runner_fin_place_perc[key1] !== undefined ) {
      setTrack1(props.horse.runner_fin_place_perc[key1]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key2] !== undefined ) {
      setTrack2(props.horse.runner_fin_place_perc[key2]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key3] !== undefined ) {
      setTrack3(props.horse.runner_fin_place_perc[key3]);
    }
  }

  const updateSurface = (surface) => {
    const key1 = `perc_horse_surface_fin_place_${surface}_1`;
    const key2 = `perc_horse_surface_fin_place_${surface}_2`;
    const key3 = `perc_horse_surface_fin_place_${surface}_3`;

    if ( typeof props.horse.runner_fin_place_perc[key1] !== undefined ) {
      setSurface1(props.horse.runner_fin_place_perc[key1]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key2] !== undefined ) {
      setSurface2(props.horse.runner_fin_place_perc[key2]);
    }

    if ( typeof props.horse.runner_fin_place_perc[key3] !== undefined ) {
      setSurface3(props.horse.runner_fin_place_perc[key3]);
    }
  }

  const changeColor= event =>{
    if (event.currentTarget.style.backgroundColor) {
      event.currentTarget.style.backgroundColor = null;
      event.currentTarget.style.color = null;
    } else {
      event.currentTarget.style.backgroundColor = 'white';
      event.currentTarget.style.color = 'black';
    }
  }
  
  const [sireMaxLine, setSireMaxLine] = React.useState(1);
  function changeSireMaxLine(){
    if(sireMaxLine == 1){
      setSireMaxLine(50)
    }
    else{
      setSireMaxLine(1)
    }
  }

  const [breederMaxLine, setbreederMaxLine] = React.useState(1);
  function changeBreederMaxLine(){
    if(breederMaxLine == 1){
      setbreederMaxLine(50)
    }
    else{
      setbreederMaxLine(1)
    }
  }

  const [ownerMaxLine, setownerMaxLine] = React.useState(1);
  function changeOwnerMaxLine(){
    if(ownerMaxLine == 1){
      setownerMaxLine(50)
    }
    else{
      setownerMaxLine(1)
    }
  }

  const [damMaxLine, setdamMaxLine] = React.useState(1);
  function changeDamMaxLine(){
    if(damMaxLine == 1){
      setdamMaxLine(50)
    }
    else{
      setdamMaxLine(1)
    }
  }

  const [jockeyMaxLine, setJockeyMaxLine] = React.useState(1);
  function changeJockeyMaxLine(){
    if(jockeyMaxLine == 1){
      setJockeyMaxLine(50)
    }
    else{
      setJockeyMaxLine(1)
    }
  }

  const [trnMaxLine, setTrnMaxLine] = React.useState(1);
  function changeTrnMaxLine(){
    if(trnMaxLine == 1){
      setTrnMaxLine(50)
    }
    else{
      setTrnMaxLine(1)
    }
  }

  const [nameMaxLine, setNameMaxLine] = React.useState(1);
  function changeNameMaxLine(){
    if(nameMaxLine == 1){
      setNameMaxLine(50)
    }
    else{
      setNameMaxLine(1)
    }
  }

  return (
    <>
    {props.isChecked? 
      <div>
        <Grid fontSize='sm' templateColumns={"repeat(21,6fr)"} gap={0}>
          <GridItem colSpan={1} rowSpan={6}>
            <Badge p='2' backgroundColor={props.horse.h_sadl_col} fontSize='25' color='white' variant='solid' marginRight='5px' float='left'>{props.position}</Badge>
          </GridItem>
          <GridItem colSpan={6} rowSpan={2}>
            <b>{props.horse.h_name}</b>
          </GridItem>
          <GridItem colSpan={4} rowSpan={2}>
            <b>{props.horse.h_weight} lbs</b>
          </GridItem>
          <GridItem colSpan={5} rowSpan={6}>
            <Box fontSize='sm'>
              {/* YEAR */}
              <Select onClick={changeColor} onChange={changeYear} size='xs' fontSize='.65rem' height='1.2rem' width='90px' textAlign='center' display='inline-block' marginRight='5px' marginBottom='3px' backgroundColor='green.600' color='white' fontWeight='bold' placeholder='YEAR'>
              {yearOptions}
              </Select>

              <b>W </b>{year1}
              <b> P </b>{year2}
              <b> S </b>{year3}
              <br />

              {/* TRACK */}
              <Select onClick={changeColor} onChange={changeTrack} size='xs' fontSize='.65rem' height='1.2rem' width='90px' textAlign='center' display='inline-block' marginRight='5px' marginBottom='3px' backgroundColor='green.600' color='white' fontWeight='bold' placeholder='TRACK'>
              {trackOptions}
              </Select>

              <b>W </b>{track1}
              <b> P </b>{track2}
              <b> S </b>{track3}
              <br />

              {/* SURFACE */}
              <Select onClick={changeColor} onChange={changeSurface} size='xs' fontSize='.65rem' height='1.2rem' width='90px' textAlign='center' display='inline-block' marginRight='5px' marginBottom='3px' backgroundColor='green.600' color='white' fontWeight='bold' placeholder='SURFACE'>
              {surfaceOptions}
              </Select>

              <b>W </b>{surface1}
              <b> P </b>{surface2}
              <b> S </b>{surface3}
              <br />
            </Box>
          </GridItem>
          <GridItem colSpan={5} rowSpan={6} fontSize='15'>
            <Badge color='white' backgroundColor='black' fontSize='40px'  width='80px' height='75'>
                <Text fontSize='xs' >ML</Text>
                <Center><Text>{props.horse.h_morn_ln}</Text></Center>
            </Badge>
            <Badge backgroundColor='green.600' color='white' fontSize='40px' width='80px' height='75'>
                <Text fontSize='xs'>Live</Text>
                <Center><Text>{props.horse.h_curr_odds}</Text></Center>
            </Badge>
          </GridItem>
          <GridItem colSpan={6} rowSpan={2}>
              {props.horse.jockey_name===null?
                <b>Jockey:</b> : 
                <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Jockey:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.jockey_name} maxLine={jockeyMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeJockeyMaxLine}/></GridItem>
                </Grid>
                }
          </GridItem>
          <GridItem colSpan={4} rowSpan={2}>
              <SimpleGrid columns={4}>
              <Box><b>W</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_1}</Box>
              <Box><b>P</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_2}</Box>
              <Box><b>S</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_3}</Box>
              </SimpleGrid>
          </GridItem>
          <GridItem colSpan={6} rowSpan={2}>
                  {props.horse.trainer_name===null?
                  <b>Trainer:</b> :
                  <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Trainer:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.trainer_name} maxLine={trnMaxLine} 
                    ellipsis='...' trimRight basedOn='letters' onClick={changeTrnMaxLine}/>
                  </GridItem>
                  </Grid>
                  }
          </GridItem>
          <GridItem colSpan={4} rowSpan={2}>
              <SimpleGrid columns={4}>
                <Box><b>W</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_1}</Box>
                <Box><b>P</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_2}</Box>
                <Box><b>S</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_3}</Box>
              </SimpleGrid>
          </GridItem>

        </Grid>
      </div>
    :
          <div>
            <div>
        <Grid fontSize='sm' templateColumns={"repeat(21,4fr)"} gap={0}>
          <GridItem colSpan={1} rowSpan={4}>
            <Badge p='2' backgroundColor={props.horse.h_sadl_col} fontSize='25' color='white' variant='solid' marginRight='5px' float='left'>{props.position}</Badge>
          </GridItem>
          <GridItem colSpan={6} >
            <b>{props.horse.h_name}</b>
          </GridItem>
          <GridItem colSpan={4} rowSpan={2} >
            <Box>
                <Badge color='white' backgroundColor='black' fontSize='sm' width='100px'>
                    <Center>
                    <HStack>
                      <Text fontSize='xs'>ML</Text>
                      <Text fontSize='20px'>{props.horse.h_morn_ln}</Text>
                    </HStack>
                    </Center>
                  </Badge>
                  <Badge backgroundColor='green.600' color='white' fontSize='sm' width='100px' >
                    <Center>
                    <HStack>
                      <Text fontSize='xs'>Live</Text>
                      <Text fontSize='20px'>{props.horse.h_curr_odds}</Text>
                    </HStack>
                    </Center>
                  </Badge>
              </Box>
          </GridItem>
          <GridItem colSpan={5}>
                {props.horse.h_dam===null?
                <b>Dam:</b> : 
                <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Dam:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.h_dam} maxLine={damMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeDamMaxLine}/></GridItem>
                </Grid>
                }
          </GridItem>
          <GridItem colSpan={5} rowSpan={4}>
            <Box fontSize='sm'>
                {/* YEAR */}
                <Select onClick={changeColor} onChange={changeYear} size='xs' fontSize='.65rem' height='1.5rem' width='90px' textAlign='center' display='inline-block' marginRight='5px' marginBottom='8px' backgroundColor='green.600' color='white' fontWeight='bold' placeholder='YEAR'>
                {yearOptions}
                </Select>

                <b>W </b>{year1}
                <b> P </b>{year2}
                <b> S </b>{year3}
                <br />

                {/* TRACK */}
                <Select onClick={changeColor} onChange={changeTrack} size='xs' fontSize='.65rem' height='1.5rem' width='90px' textAlign='center' display='inline-block' marginRight='5px' marginBottom='8px' backgroundColor='green.600' color='white' fontWeight='bold' placeholder='TRACK'>
                {trackOptions}
                </Select>

                <b>W </b>{track1}
                <b> P </b>{track2}
                <b> S </b>{track3}
                <br />

                {/* SURFACE */}
                <Select onClick={changeColor} onChange={changeSurface} size='xs' fontSize='.65rem' height='1.5rem' width='90px' textAlign='center' display='inline-block' marginRight='5px'  backgroundColor='green.600' color='white' fontWeight='bold' placeholder='SURFACE'>
                {surfaceOptions}
                </Select>

                <b>W </b>{surface1}
                <b> P </b>{surface2}
                <b> S </b>{surface3}
                <br />
              </Box>
          </GridItem>
          <GridItem colSpan={4}>
          <b>{props.horse.h_weight} lbs</b>
          </GridItem>


          <GridItem colSpan={5} >
                {props.horse.h_sire===null?
                <b>Sire:</b> : 
                <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Sire:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.h_sire} maxLine={sireMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeSireMaxLine}/></GridItem>
                </Grid>
                }
          </GridItem>

          <GridItem colSpan={6} >
              {props.horse.jockey_name===null?
                <b>Jockey:</b> : 
                <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Jockey:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.jockey_name} maxLine={jockeyMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeJockeyMaxLine}/></GridItem>
                </Grid>
                }
          </GridItem>
          <GridItem colSpan={4} >
              <SimpleGrid columns={4}>
              <Box><b>W</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_1}</Box>
              <Box><b>P</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_2}</Box>
              <Box><b>S</b>{props.horse.runner_fin_place_perc.perc_jockey_fin_place_3}</Box>
              </SimpleGrid>
          </GridItem>

          <GridItem colSpan={5} >
              {props.horse.owner_name===null?
                <b>Owner: </b> :
                <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Owner:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.owner_name} maxLine={nameMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeNameMaxLine}/></GridItem>
                </Grid>
              }
          </GridItem>

          <GridItem colSpan={6} >
                  {props.horse.trainer_name===null?
                  <b>Trainer:</b> :
                  <Grid templateColumns={"repeat(10,1fr)"}>
                  <GridItem colSpan={1}><b>Trainer:</b>&nbsp;</GridItem>
                  <GridItem colSpan={9}><LinesEllipsis text={props.horse.trainer_name} maxLine={trnMaxLine} 
                    ellipsis='...' trimRight basedOn='letters' onClick={changeTrnMaxLine}/>
                  </GridItem>
                  </Grid>
                  }
          </GridItem>
          <GridItem colSpan={4} >
              <SimpleGrid columns={4}>
                <Box><b>W</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_1}</Box>
                <Box><b>P</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_2}</Box>
                <Box><b>S</b>{props.horse.runner_fin_place_perc.perc_trainer_fin_place_3}</Box>
              </SimpleGrid>
          </GridItem>

          <GridItem colSpan={5} >
              {props.horse.breeder_name===null?
                <b>Breeder: </b> :
                <Grid templateColumns={"repeat(10,1fr)"}>
                <GridItem colSpan={1}><b>Breeder:</b>&nbsp;</GridItem>
                <GridItem colSpan={9}><LinesEllipsis text={props.horse.breeder_name} maxLine={breederMaxLine} 
                  ellipsis='...' trimRight basedOn='letters' onClick={changeBreederMaxLine}/>
                </GridItem>
                </Grid>
              }
          </GridItem>

        </Grid>
      </div>
      </div>
      }

      <div className='clearFix'></div>
      
    </>
  );
}

